<template>
  <div>
    <div v-for="(item, index) in data" :key="index">
      <t-carousel
      v-if="item.type === 'carousel'"
      :data="item.data"
      :options="item.options"
      @click="click"
      ></t-carousel>
      <t-grid
      v-else-if="item.type === 'grid'"
      :data="item.data"
      @click="click"
      ></t-grid>
      <t-notice
      v-else-if="item.type === 'notice'"
      :data="item.data"
      :options="item.options"
      @click="click"
      ></t-notice>
      <t-row
      v-else-if="item.type === 'row'"
      :data="item.data"
      @click="click"
      ></t-row>
      <t-card
      v-else-if="item.type === 'card'"
      :options="item.options"
      @click="click"
      ></t-card>
      <t-title
      v-else-if="item.type === 'title'"
      :options="item.options"
      @click="click"
      ></t-title>
    </div>
  </div>
</template>
<script>
import {tCard, tCarousel, tGrid, tNotice, tRow, tTitle} from 'team-ui'
export default {
  components: {
    tCard,
    tCarousel,
    tGrid,
    tNotice,
    tRow,
    tTitle
  },
  props: {
    data: {
      type: Array,
      default: []
    }
  },
  methods: {
    click (e) {
      if (e.linkType === '0' || e.linkType === 0) {
        T.Native.pushWindow(
            {
              data : {
                  path : e.link,
                  backPage : -1,
                  params : {} ,
              },
              options : {
                  transparentTitle : false,
                  showTitleBar : true,
              }    
            },
        )
      }
      this.$emit('click', e)
    }
  }
}
</script>



