<template>
  <t-page ref="page">
    <!-- <t-header slot="top" :leftOptions="[]"></t-header> -->
    <bscroll
    ref="bscroll"
    :pullDownRefresh="{threshold: 50,stop: 50, txt: ''}"
    @pullingDown="pullDown">
      <div class="top">
        <div class="btn-login" @click="jumpLogin">0</div>
      </div>
      <t-all :data="data" @click="click"></t-all>
    </bscroll>
  </t-page>
</template>
<script>
import tAll from '@extend/grid'
import bscroll from '@extend/Bscroll'
export default {
  components: {
    bscroll,
    tAll
  },
  data () {
    return {
      pullDownState: true,
      data: []
    }
  },
  methods: {
    pullDown () {
      this.pullDownState = false
      this.request({
        head:{
          timestamp: (new Date()).getTime()
        },
        data:{
          modelCode:'page_three'
        }
      }, () => {
        setTimeout(() => {
          this.$refs.bscroll.forceUpdate(false)
        }, 500)
      })
    },
    click (e) {
      // this.$toast({
      //   msg: ` ${e.linkType},  ${e.link}`
      // })
    },
    request (opt, callback) {
      T.Native.networkRequest({
        data: {
          url: '/operate/manager/queryModelList',
          params: opt
        }
      }).then((data) => {
        this.data = JSON.parse(data[0].json)
        callback && callback()
      })
    },
    jumpLogin () {
      let jumpUrl = '/login/loginPwd/index'
      T.Native.pushWindow(
            {
              data : {
                  path : jumpUrl,
                  backPage : -1,
                  params : {},
              },
              options : {
                  transparentTitle : false,
                  showTitleBar : false,
              }    
            },
        )
    },
  },
  mounted() {
    this.request({
      head:{
        timestamp: (new Date()).getTime()
      },
      data:{
        modelCode:'page_three'
      }
    })
  },
}
</script>
<style lang="scss" scoped>
.top {
    //margin-top: px2rem(17);
    height: px2rem(246);
    background: url("../images/top-bg.png") no-repeat center;
    background-size: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .btn-login {
        width: px2rem(300);
        height: px2rem(80);
        background-color: #ffffff;
        border-radius: px2rem(40);
        color: #4B80EE;
        font-size: px2rem(32);
        font-family: PingFang-SC-Medium;
        text-align: center;
        line-height: px2rem(80);
        @include button-color(#ffffff, #4B80EE)
    }
}
.loading{
        position: absolute;
    height: 0.5rem;
    width: 100%;
    text-align: center;
    margin: 0.2rem auto;
    top: -0.9rem;
    >img{
      height: 100%;
    }
  }
</style>




